/*cart*/

.cart {
    margin-bottom: 10px;
    font-size: 14px;
    background-color: $white-color;
    color: $tre-color;
    &-img {
        @include size(108px,
        108px);
        margin: 70px auto 16px;
    }
    &-text {
        color: $nine-color;
    }
    &-back {
        @include btn(180px,40px,$deputy-color,4px);
        margin: 30px auto;
    }
    &-list {
        padding: 10px;
        border-bottom: 1px solid $border-color;
    }
    &-del {
        color: $nine-color;
    }
    &-label {
        width: 70px;
    }
    &-ipt {
        flex: 1;
        &.input::-webkit-input-placeholder {
            color: $nine-color;
        }
    }
    &-kind {
        border-bottom: 1px solid $border-color;
    }
    &-name {
        margin: 4px 0;
    }
    &-price {
        color: $deputy-color;
        font-weight: 600;
    }
    &-totle {
        color: $deputy-color;
        font-weight: bold;
    }
    &-lj {
        @include before('-',
        $deputy-color,
        inline-block);
    }
    &-yf {
        @include before('+',
        $deputy-color,
        inline-block);
    }
    &-shop {
        padding: 6px 10px;
        border-bottom: 1px solid $border-color;
    }
}

.js-form,
.js-order,
.js-all {
    outline: none!important;
    appearance: none;
    border: none;
    &:after {
        content: '';
        display: inline-block;
        width: 16px;
        height: 16px;
        background-image: url(../img/icon-choice.png);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        background-position: top center;
    }
    &:checked:after {
        background-image: url(../img/icon-choice-hov.png);
    }
}

.js-all {
    margin-top: 6px!important;
}

.account {
    position: relative;
    @include size(100%,
    50px);
    font-size: 14px;
    @include colbg($tre-color,
    $white-color);
    box-shadow: 0 -1px 1px #efefef;
    &-choose {
        width: 60px;
    }
    &-choice {
        padding: 4px 0 0 22px;
    }
    &-price {
        flex: 1;
        font-weight: 600;
        color: $deputy-color;
        padding: 0 20px;
        @include before('总计：',
        $tre-color,
        inline-block);
        &:before {
            font-weight: normal;
        }
    }
    &-btn {
        @include bigbtn(100px,
        50px,
        $gray-color,
        $white-color,
        0);
        &.btn_sty {
            background-color: $deputy-color;
        }
    }
    &-reverse {
        @include btn(100px,
        50px,
        $deputy-color,
        0);
        &.btn_sty {
            background-color: gray;
        }
    }
}

.deliver {
    &-box {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px 10px;
        margin-bottom: 10px;
        font-size: 14px;
        background-color: #fff;
        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            @include size(100%,2px);
            background: url(../img/divider_blue_red.png) repeat-x center;
            background-size: contain;
        }
    }
    &-arrow {
        margin-left: 10px;
    }
}