.shop {
	&_head {
		position: fixed;
		top: 0;
		left: 0;
		@include size($ful-width,50px);
		z-index: 33;
	}
	&_icon {
		.iconfont {
			font-size: 20px;
			margin-left: 10px;
		}
		.icon-guanzhu1 {
			color: $deputy-color;
		}
	}
	&_main {
		position: relative;
		overflow-x: hidden;
		overflow-y: auto;
		height: auto;
		background: url('https://1919-new-bbc-pro-oss.1919.cn/6079f124-7830-4f70-81a8-aa20fb9f3c20.png') no-repeat top center/contain rgb(246,246,246);
		&.fixed {
			background: none;
			.shop_top {
				position: fixed;
				top: 0;
				z-index: 22;
				box-shadow: 0 1px 1px $border-color;
				background-color: rgba($color: #fff, $alpha: 0.95);
				color: $tre-color;
				.shop_search {
					color: $six-color;
					background-color: $border-color;
					input {
						color: $six-color;
						@include placehold($six-color);
					}
				}
				.shop_item {
					&.active {
						color: $deputy-color;
					}
					&.active:after {
						background-color: $deputy-color;
					}
				}
			}
		}
	}
	&_top {
		position: absolute;
		top: 50px;
		left: 0;
		width: 100%;
		height: 50px;
		padding: 10px 3%;
		color: $white-color;
	}
	&_search,
	&_nav {
		width: 50%;
	}
	&_search {
		display: flex;
		height: 30px;
		align-items: center;
		background-color: rgba(255,255,255,.4);
		border-radius: 6px;
		i,
		span {
			margin: 0 6px;
		}
		span {
			width: 30px;
			text-align: center;
		}
		input {
			flex: 1;
			width: 100%;
			height: 100%;
			line-height: 100%;
			background-color: transparent;
			color: $white-color;
			@include placehold($white-color);
		}
	}
	&_nav {
		display: flex;
		justify-content: space-around;
		height: 30px;
		align-items: center;
		padding-left: 3%;
	}
	&_item {
		position: relative;
		font-size: 14px;
		&.active:after {
			content: '';
			position: absolute;
			bottom: -4px;
			left: 50%;
			transform: translateX(-50%);
			@include size(20px,1px);
			background-color: $white-color;
		}
	}
	&_unit {
		margin-top: 110px;
	}
	&_cell {
		display: none;
		&.active {
			display: block;
		}
	}
}